export default {
	props: {
		type: {
			type: String,
			default: '&#xe644;'
		},
		color: {
			type: String,
			default: '#666666'
		},
		fontSize: {
			type: String,
			default: '34rpx'
		}
	},
	methods: {
		toclick() {
			this.$emit('click')
		}
	}
}
